/**
 * Font icon styles belong here
 */

@font-face {
    font-family: 'ramsey';
    src:    url('../fonts/ramsey.eot?tey8vy');
    src:    url('../fonts/ramsey.eot?tey8vy#iefix') format('embedded-opentype'),
        url('../fonts/ramsey.ttf?tey8vy') format('truetype'),
        url('../fonts/ramsey.woff?tey8vy') format('woff'),
        url('../fonts/ramsey.svg?tey8vy#ramsey') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'ramsey' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-bars:before {
    content: "\e900";
}
.icon-book:before {
    content: "\e901";
}
.icon-coins:before {
    content: "\e902";
}
.icon-currency:before {
    content: "\e903";
}
.icon-hand:before {
    content: "\e904";
}
.icon-house:before {
